.link {
  text-decoration: none;
  color: black;
  display: inline-block;
  text-align: center;
}

.card {
  border: 1px solid black;
  padding: 1em;
  box-sizing: border-box;
  width: 20vw;
  margin-top: 1.5em;
  text-align: left;
}

.header {
  font-size: 1.2em;
}

.medium {
  font-size: 1em;
  font-family: 'proximaNovaReg';
}

.title {
  font-family: 'proximaNovaBold';
  font-style: italic;
}

.year {
  font-family: 'proximaNovaReg'; 
}

.description {
  font-family: 'proximaNovaReg';
  font-size: 0.9em;
  margin-top: 1em;
}

.image {
  height: 20vh;
}